<template>
  <div id="app">
    <header>
        <h1>SuhEugene</h1>
        <span class="easter-egg-1">Я есть Юджин</span>
    </header>
    <div class="hwrap"></div>
    <transition :name="transitionName"><router-view/></transition>
  </div>
</template>
<script>
export default {
  name: 'App',
  data () {
    return {
        top: -50,
        left: 0,
        transitionName: "down"
    }
  },
  watch: {
    '$route' (to, from) {
      let toDepth = to.path.split('/');
      let fromDepth = from.path.split('/');
      if (toDepth[toDepth.length-1] == "") toDepth = toDepth.slice(0,toDepth.length-1);
      if (fromDepth[fromDepth.length-1] == "") fromDepth = fromDepth.slice(0,fromDepth.length-1);
      this.transitionName = toDepth.length < fromDepth.length ? 'down' : 'up';
    }
  },
  created () {
  }
}
</script>
