import Vue from 'vue'
import VueRouter from 'vue-router'
import Home from '../views/Home.vue'
import PageFOF from '../views/PFOF.vue'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'home',
    component: Home
  },
  {
    path: '/discord',
    name: 'discord',
    component: () => import(/* webpackChunkName: "knowledge" */ '../views/Discord.vue')
  },
  {
    path: '/minecraft',
    name: 'minecraft',
    component: () => import(/* webpackChunkName: "knowledge" */ '../views/Minecraft.vue')
  },
  {
    path: '/vue',
    name: 'vue',
    component: () => import(/* webpackChunkName: "knowledge" */ '../views/Vue.vue')
  },
  {
    path: '/thanks',
    name: 'thanks',
    component: () => import('../views/Thanks.vue')
  },
  {
    path: '*',
    name: "FOF",
    component: PageFOF
  }
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes
})

export default router
